@import './../../../../../styles/mixin.scss';
@import './../../../../../styles/vars.scss';

.question {
    height: 100vh;

    .wrapper {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        .inner {
            background-color: $primaryColor;
            max-width: 413px;
            width: 100%;
            border-radius: 30px;
            padding: 36px 30px 60px;
            max-height: 600px;
            // height: 100%;
            .container {
                max-height: 141px;
                // height: 100%;
                overflow: auto;
                margin: 30px 0;
                .text1 {
                    @include text (16px, 400, 24px);
                    .span {
                        @include text (16px, 600, 24px);
                    }
                }
                .text2 {
                    margin: 33px 0 48px;
                    @include text (16px, 400, 24px);
                    .span {
                        @include text (16px, 600, 24px);
                    }
                }
            }

            .radioButton {
                margin-bottom: 68px;

                .title {
                    @include text (16px, 600, 24px);
                }

                .radio {
                    position: relative;
                    margin-top: 12px;

                    .radioInput {
                        appearance: none;
                        position: absolute;
                    }

                    .radioLabel {
                        padding-left: 25px;
                        @include text (16px, 400, 24px);
                        color: $textColor;
                        cursor: pointer;

                        &::before {
                            content: "";
                            display: block;
                            width: 16px;
                            height: 16px;

                            border: 1px solid $textColor;
                            box-sizing: border-box;
                            background-color: transparent;
                            border-radius: 50%;

                            position: absolute;
                            top: 4px;
                            left: 0;
                            z-index: 1;

                        }

                        &::after {
                            content: "";
                            display: block;
                            width: 16px;
                            height: 16px;

                            border: 4px solid $accentColor;
                            box-sizing: border-box;
                            background-color: transparent;
                            border-radius: 50%;

                            position: absolute;
                            top: 4px;
                            left: 0;
                            z-index: 2;

                            opacity: 0;
                        }
                    }

                    .radioInput:checked+.radioLabel::after {
                        opacity: 1;
                    }
                }
            }

            .btn {
                display: flex;
                justify-content: space-between;
            }
        }
    }
}